        .tarjeta.p-4.h-100(style="background-color: #c6e9f3 ")
          .row.mb-4
            .col-2: img(src='@/assets/curso/temas/27.svg', alt='')
          p.mb-0 Las antenas son otro pilar fundamental en la infraestructura inalámbrica, ya que determinan la calidad y el alcance de la señal. La elección de la antena adecuada —omnidireccional o direccional— depende del entorno y los objetivos de cobertura. Las antenas omnidireccionales, que emiten señales en todas direcciones, son ideales para cubrir áreas circulares o pequeñas, mientras que las antenas direccionales enfocan la señal hacia una zona específica, maximizando el alcance en esa dirección. La correcta instalación y orientación de las antenas es fundamental para evitar zonas muertas (dead zones) y lograr una cobertura uniforme.
      .col-lg-6.mb-lg-0.mb-3
        .tarjeta.p-4.h-100(style="background-color: #f3f0ea ")
