            .col-2: img(src='@/assets/curso/temas/28.svg', alt='')
          p.mb-0 Para diseñar redes inalámbricas eficientes, es necesario realizar estudios de cobertura conocidos como site surveys, que permiten identificar los mejores puntos para colocar los APs y las antenas. Estas evaluaciones consideran factores físicos del entorno, como paredes, techos, materiales de construcción y posibles interferencias. Realizar un site survey permite prever problemas y asegurarse de que la red ofrecerá la máxima cobertura posible, minimizando las áreas sin señal y evitando interferencias que puedan afectar el rendimiento de la red.
    div(style="background-color: #efe8f7 ")
      .row.justify-content-center.mb-5.g-0
